@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

html, body, #root {
  min-height: 100%;
  background-color: rgb(233, 225, 225);
}


body, input, button {
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  font-size: 14px;
}
.card-container {
  margin: 80px auto 0;
  margin-top: 150px;
  max-width: 600px;
  width: 100%;
}

.card-content {
  width: 100%;
  border-radius: 4px;
  padding: 30px;
}
.btn-main {
  height: 150px;
  font-size: 30px;
  line-height: 1.5em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-container {
  margin: 50px auto 0;
  max-width: 600px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0px 2px 4px 0 #ccc;
  background: #ccc;
}

.form-content {
  width: 100%;
  border-radius: 4px;
  padding: 30px;
}
.gabarito{
  border: 2px solid #000;
  background-color: #000;
  color: aliceblue;
  width: 80px;
  height: 80px;
  border-radius: 80%;
  margin: 5px;
}

.teclado{  
  /* border: 2px solid red; */
  margin: 0 150px;
  height: 500px;
  background-color: #555;
  padding: 5px 5px 20px;
  border-radius: 5px;
  box-shadow: 0px 4px 8px -4px #000;
  margin-bottom: 30px;
}

.slider {
  position: relative;
  width: 100%;
  margin: 0 auto;
  height: 550px;
  overflow: hidden;
  white-space: nowrap;
}

.slider-wrapper {
  position: relative;
  height: 100%;
  width: 100%;
  /* margin-bottom: 300px; */
}

.slide {
  display: inline-block;
  height: 100%;
  width: 100%;
}

.arrow {
  height: 150px;
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #919b93;
  border-radius: 50%;
  cursor: pointer;
  -webkit-transition: -webkit-transform ease-in .1s;
  transition: -webkit-transform ease-in .1s;
  transition: transform ease-in .1s;
  transition: transform ease-in .1s, -webkit-transform ease-in .1s;
}

.nextArrow {
  position: absolute;
  top: 55%;
  right: 100px;
  z-index: 999;
  color: #fff;
}

.backArrow {
  position: absolute;
  top: 55%;
  left: 100px;
  z-index: 999;
  color: #fff;
}

.buttonConfirmContent {
  height: 150px;
  width: 300px;
  position: absolute;
  top: 55%;
  left: 38%;
  z-index: 999;
  background-color: rgb(137, 13, 148);
  font-size: 4em;
  color: whitesmoke;
  line-height: 2.5;
}

.fa-arrow-right:before, .fa-arrow-left:before {
  color:black;
}

.alternativa {
  width: 200px;
  height: 200px;
  margin: 10px auto;
  
}

.btn-alternativa {
  width: 100%;
  height: 100%;
  font-size: 100px;
  color: whitesmoke;
  line-height: 1.8;
}

.vermelho {
  background-color: #d62c2c;
}
.azul {
